@charset "UTF-8";
@font-face {
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  font-family: "Lora";
  src: url("../fonts/Lora-Regular.woff") format("woff");
}
body {
  font-family: "Noto Serif JP", serif;
  color: #5d5d5d;
  position: relative;
}

.l-body {
  min-height: 100vh;
  display: grid;
  grid-template-rows: 1fr auto;
  grid-template-columns: 100%;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
